<template>
  <div class="jd-header">
    <div v-if="$route.name !== 'home'" class="place-head"></div>
    <div
      ref="jdHeader"
      class="navBarWrap j-shadow-sm"
      :class="whiteNav || $route.name !== 'home' ? 'white-bg' : 'logo-none'"
    >

      <a class="nav-logo j-inline-flex j-items-center" @click="goHome">
        <img
          :src="imgUrl.logo_jd"
          class="nav-img"
        />
        <span
          class="j-ml-1 j-text-2xl logo-text"
        >个人博客-vlog</span>
      </a>
      <ul class="header-nav">
        <li
          v-for="(item, idx) in tabName"
          :key="item.id"
          :class="[
            whiteNav || $route.name !== 'home' ? 'black-ft' : '',
            { currentStyle: item.includeName.includes($route.name) },
          ]"
          class="header-nav-item j-inline-block j-text-center"
          @click="goNav(item, idx)"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {
  defineComponent,
  reactive,
  toRefs,
  onMounted,
  onUnmounted,
} from '@vue/composition-api';
import { imgUrl } from '../../views/home/mock/home-mock';
export default defineComponent({
  setup(_, { root: { $router } }) {
    const data = reactive({
      whiteNav: false,
      tabName: [
        {
          id: 1,
          name: '首页',
          pathName: 'home',
          includeName: ['home'],
        },
        {
          id: 2,
          name: '产品',
          pathName: 'product-list',
          includeName: ['product-list', 'product-detail'],
        },
        {
          id: 3,
          name: '文章',
          pathName: 'article',
          includeName: ['article'],
        },
      ],
    });
    onMounted(() => {
      addEvent();
    });
    const addEvent = () => {
      window.addEventListener('scroll', handleScroll);
    };
    onUnmounted(() => {
      window.removeEventListener('scroll', handleScroll);
    });
    const goHome = () => {
      $router.push({ name: 'home' });
    };
    //滚动事件
    const handleScroll = (event) => {
      //获取导航到顶部的距离
      const scrollTop = event.srcElement.scrollingElement.scrollTop;
      // console.log(scrollTop, '****');
      if (scrollTop > 120) {
        data.whiteNav = true;
      } else {
        data.whiteNav = false;
      }
    };
    const goNav = (item) => {
      $router.push({ name: item.pathName });
    };
    return {
      goNav,
      goHome,
      addEvent,
      imgUrl,
      handleScroll,
      ...toRefs(data),
    };
  },
});
</script>

