.jd-header {
  width: 100%;

  .white-bg {
    background: #fff;
  }

  .currentStyle {
    border-bottom: 2px solid #2a87fc;
    color: #2a87fc;
  }
  .place-head {
    width: 100%;
    height: 120px;
  }

  .navBarWrap {
    @apply j-flex j-items-center;

    position: fixed;
    top: 0;
    z-index: 999;
    width: 100%;

    .nav-logo {
      vertical-align: top;
      margin-left: 120px;
    }

    .nav-img {
      cursor: pointer;
    }

    .header-nav {
      margin-left: 128px;

      .black-ft {
        color: #000;
      }

      &-item {
        vertical-align: middle;
        font-size: 14px;
        line-height: 120px;
        color: #fff;
        width: 40px;
        margin-right: 80px;
      }

      &-item:hover {
        color: #2a87fc;
        cursor: pointer;
      }
    }
  }

  .logo-none {
    .logo-text {
      color: white;
    }
  }
}
</style>
