import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Routes from './routes';

Vue.use(VueRouter)

const routes = [
  ...Routes,
]

const router = new VueRouter({
  // mode: 'history',
  routes,
  scrollBehavior(to, from) {
    if (to.name !== from.name) {
      return { x: 0, y: 0 };
    }
  },
})

export default router
